"use client";

import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { collection, getDocs, addDoc } from "firebase/firestore";
import { db } from "@/lib/firebase";

interface Vehicle {
  id: string;
  placa: string;
  modelo: string;
  anoFabricacao: string;
  quilometragem: number;
  centroCusto: string;
  status: "ativo" | "manutencao";
  ultimaManutencao: string;
  observacao?: string;
}

interface CostCenter {
  id: string;
  nome: string;
}

export default function VeiculosPage() {
  const [vehicles, setVehicles] = useState<Vehicle[]>([]);
  const [costCenters, setCostCenters] = useState<CostCenter[]>([]);
  const [newVehicle, setNewVehicle] = useState({
    placa: "",
    modelo: "",
    anoFabricacao: "",
    quilometragem: 0,
    centroCusto: "",
    observacao: "",
  });
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      try {
        // Fetch vehicles
        const vehiclesSnap = await getDocs(collection(db, "Veiculos"));
        const vehiclesData = vehiclesSnap.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
        })) as Vehicle[];
        setVehicles(vehiclesData);

        // Fetch cost centers
        const costCentersSnap = await getDocs(collection(db, "centroCusto"));
        const costCentersData = costCentersSnap.docs.map(doc => ({
          id: doc.id,
          nome: doc.data().nome,
        }));
        setCostCenters(costCentersData);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchData();
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const docRef = await addDoc(collection(db, "Veiculos"), {
        ...newVehicle,
        status: "ativo",
        ultimaManutencao: new Date().toISOString(),
      });

      setVehicles([...vehicles, {
        id: docRef.id,
        ...newVehicle,
        status: "ativo",
        ultimaManutencao: new Date().toISOString(),
      }]);

      setNewVehicle({
        placa: "",
        modelo: "",
        anoFabricacao: "",
        quilometragem: 0,
        centroCusto: "",
        observacao: "",
      });
      setIsDialogOpen(false);
    } catch (error) {
      console.error("Error adding vehicle:", error);
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold">Veículos</h1>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button className="bg-black text-white hover:bg-gray-800">
              + Novo Veículo
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Novo Veículo</DialogTitle>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <Input
                  placeholder="Placa"
                  value={newVehicle.placa}
                  onChange={(e) => setNewVehicle({
                    ...newVehicle,
                    placa: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  placeholder="Modelo"
                  value={newVehicle.modelo}
                  onChange={(e) => setNewVehicle({
                    ...newVehicle,
                    modelo: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  placeholder="Ano de Fabricação"
                  value={newVehicle.anoFabricacao}
                  onChange={(e) => setNewVehicle({
                    ...newVehicle,
                    anoFabricacao: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  type="number"
                  placeholder="Quilometragem"
                  value={newVehicle.quilometragem}
                  onChange={(e) => setNewVehicle({
                    ...newVehicle,
                    quilometragem: parseInt(e.target.value) || 0,
                  })}
                  required
                />
              </div>
              <div>
                <Select
                  value={newVehicle.centroCusto}
                  onValueChange={(value) => setNewVehicle({
                    ...newVehicle,
                    centroCusto: value,
                  })}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Centro de Custo" />
                  </SelectTrigger>
                  <SelectContent>
                    {costCenters.map((center) => (
                      <SelectItem key={center.id} value={center.id}>
                        {center.nome}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div>
                <Input
                  placeholder="Observação"
                  value={newVehicle.observacao}
                  onChange={(e) => setNewVehicle({
                    ...newVehicle,
                    observacao: e.target.value,
                  })}
                />
              </div>
              <Button type="submit" className="w-full bg-black text-white hover:bg-gray-800">
                Salvar
              </Button>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      <div className="grid grid-cols-2 gap-6">
        {vehicles.map((vehicle) => (
          <div
            key={vehicle.id}
            className="bg-white rounded-lg shadow p-6"
          >
            <div className="flex justify-between items-start">
              <div>
                <div className="flex items-center space-x-4 mb-2">
                  <h3 className="font-medium">{vehicle.placa}</h3>
                  <span className={`px-3 py-1 rounded-full text-sm ${
                    vehicle.status === "ativo"
                      ? "bg-green-100 text-green-800"
                      : "bg-yellow-100 text-yellow-800"
                  }`}>
                    {vehicle.status === "ativo" ? "Ativo" : "Em manutenção"}
                  </span>
                </div>
                <p className="text-sm text-gray-500">
                  {vehicle.modelo} {vehicle.anoFabricacao}
                </p>
                <p className="text-sm text-gray-500">
                  Quilometragem: {vehicle.quilometragem?.toLocaleString ? vehicle.quilometragem.toLocaleString() : vehicle.quilometragem} km
                </p>
                {vehicle.observacao && (
                  <p className="text-sm text-gray-500 mt-2">
                    {vehicle.observacao}
                  </p>
                )}
              </div>

              <Button variant="ghost" size="sm">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="20"
                  height="20"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                >
                  <path d="M17 3a2.85 2.83 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3Z" />
                </svg>
              </Button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
